* {
  box-sizing: border-box;
}

.navbar {
  font-size: 2rem;
  padding: 1em;
  background-color: blanchedalmond;
}
.navbar-logo {
  color: green;
  margin-right: 0.5em;
}

.add-form {
  margin: 1em 0 0 2em;
}
.add-input {
  font-size: 2rem;
  margin-right: 0.2em;
}

.add-button {
  height: 100%;
  font-size: 1.5rem;
  padding: 0.3em 0.5em;
  flex-direction: column;
  text-align: center;
  background-color: darkgreen;
  color: aliceblue;
}

button {
  outline: none;
  border: 0px;
  cursor: pointer;
}
.habit {
  font-size: 2.5rem;
  padding: 0.5em;
  list-style: none;
}
.habit-count {
  display: inline-block;
  text-align: center;
  font-size: 2rem;
  width: 2.5rem;
  height: 2.5rem;
  line-height: 2.5rem;
  margin-left: 0.2em;
  margin-right: 0.5em;
  background-color: darkseagreen;
  color: white;
  border-radius: 50%;
}
.habit-button {
  font-size: 2.5rem;
  margin: 0 0.1em;
  background-color: transparent;
}
.habit-button:hover {
  opacity: 0.7;
}

.habit-increase,
.habit-decrease {
  color: goldenrod;
}

.habit-delete {
  font-size: 2rem;
  color: darkred;
}
.habits-reset {
  font-size: 1.5rem;
  padding: 0.2em 1em;
  background-color: goldenrod;
  margin-left: 2em;
  color: aliceblue;
}
